import dgmhdtable from './dgm_hdtable.js';
import {getDgmInfo,getMHDInfo} from './dgm_info.js'
import {dgmBtn_name,reRenderSelectOption} from "@/components/content/system/dgm/table/js/dgm_methods";
import {distinct,compareName} from "@/utils/utils";
export const dgmMixin={
  data(){
    return{
        hot: {},//hottable
        data: [],//hottabledata

        changeList:{
          changeData:{},//
          sortMacList:[],//
          sortDgmList:[],//
          changeMhd:{},//MHD
          changeTms:{},//tms
          addMhd:[],//MHD
          addTms:[],//tms
          deleteData:[],//mhd
          addDefaultDgm: [],//type==18)
          isValidObj:{
            isValidResult: true,//
            prop:'',
            row:'',
          },

        },
        MHDList:[],//mhd
        E821List:[],//E821
        tmsList:{},//tms
        tmsHostList:{},//tms
        tmsPumpList:{},//tms
        tmsPreIdList:[],//tms//id
        dgmList:{},//
        defaultDgmInfo:{},//
        flangeList:{},//
        scroll:0,//
        timer: null,//
    }
  },
  destroyed(){
    this.$store.commit('clearSroll','dgm')
    if(this.timer){
      clearTimeout(this.timer);
    }
  },
  activated(){
    let wtHolder = document.getElementsByClassName('wtHolder')[0]
    if(wtHolder){
      let state = this.$store.state.scroll['dgm'][this.type]
      wtHolder.scrollTop = state === undefined ? 0 :state.scrollTop
      wtHolder.scrollLeft = state === undefined ? 0 :state.scrollLeft
    }
  },
  beforeRouteLeave (to, from, next) {
    if(to.name==='fitInfo'){
      if(this.$parent.fittingInfo.length===0){
        swal({
          title: "",
          text: ``,
          button: "",
        });
        return;
      }
      else{
        this.$parent.fitShow = true;
        this.$parent.fitBtnShow = false;
        next();
      }
    }
    if(!$.isEmptyObject(this.changeList.changeData)){
      swal({
        title: "",
        text: `${dgmBtn_name(this.type)}`,
        buttons: {
          sure: "",
          cancel: "",
        },
        dangerMode: true,
      })
          .then((willDelete) => {
            if (willDelete) {
              next();
            } else {
              window.history.go(1);
            }
          });
    }
    else{
      next();
    }
  },
  mounted(){
    this.getListData(this.type);
  },
  methods:{
    getListData(type){
      let loadTime = false;
      let that = this;
      this.timer = setTimeout(function(){//
        if(!loadTime){
          that.$parent.loading = true;//
        }
      },500)
      if(type==11){
        this.$getApi.getMHDDgmInfo()
            .then(res=>{
              loadTime = true;
              this.$parent.loading = false;//
              let result = getMHDInfo(res,type)[0];
              let canBuild = getMHDInfo(res,type)[1];
              this.MHDList = getMHDInfo(res,type)[2];
              if(!canBuild){
                this.$emit("child-msg",this.type,false)
              }else{
                //dgm

                this.data = result;
                //
                this.$emit("child-msg", this.type,true)
                // this.filter_by_dgmName();
                this.renderTable()
              }
            })
            .catch(err=>{
              console.log('' ,err)
            })
      }
      else if(type==13 || type==14 || type==15){
        let params = type - 12
        this.$getApi.queryBoltList(params,this.$store.state.tids)
            .then(res=>{
              loadTime = true;
              this.$parent.loading = false;//
              let result = getDgmInfo(res,type);
              //dgm
              if(type==14){
                this.dgmList={};
                let dgmInfo = res.dgm_list;
                if(dgmInfo.length){
                  dgmInfo.forEach(dgm=>{
                    this.dgmList[dgm.dgm_id] = dgm.dgm_name;
                  })
                }
                else{
                  this.$emit("child-msg",this.type,false)
                }

              }
              if(type==15){
                this.dgmList={};
                this.flangeList={};
                let dgmInfo = res.dgm_list;
                if(dgmInfo.length){
                  dgmInfo.forEach(dgm=>{
                    this.dgmList[dgm.dgm_id] = dgm.dgm_name;
                    this.flangeList[dgm.dgm_id] = {};
                    this.flangeList[dgm.dgm_id][255] = '';
                    dgm.flangeList.forEach(flange=>{
                      this.flangeList[dgm.dgm_id][flange.byId] = flange.byName;
                    })
                  })
                }
                else{
                  this.$emit("child-msg",this.type,false)
                }
              }
              if(!result.length && type==13){
                this.$emit("child-msg",this.type,false)
              }else{
                this.data = result;
                //
                this.$emit("child-msg", this.type,true)
                // this.filter_by_dgmName();
                this.renderTable()
              }
            })
            .catch(err=>{
              console.log('' ,err)
            })
      }
      else if(type==16){
        this.$getApi.queryBoltChannel(this.$store.state.tids)
            .then(res=>{
              loadTime = true;
              this.$parent.loading = false;//
              let result = getDgmInfo(res,type)

              if(!result.length){
                this.$emit("child-msg",this.type,false)
              }else{
                this.data = result;
                //
                this.$emit("child-msg", this.type,true)
                // this.filter_by_dgmName();
                this.renderTable()
              }
            })
            .catch(err=>{
              console.log('' ,err)
            })
      }
      else if(type==17){
        this.$getApi.getE821DgmInfo()
            .then(res=>{
                loadTime = true;
                this.$parent.loading = false;//
                let result = getMHDInfo(res,type)[0];
                let canBuild = getMHDInfo(res,type)[1];
                this.E821List = getMHDInfo(res,type)[2];
                if(!canBuild){
                  this.$emit("child-msg",this.type,false)
                }else{
                  //dgm

                  this.data = result;
                  //
                  this.$emit("child-msg", this.type,true);
                  // this.filter_by_dgmName();
                  this.renderTable()
                }
            })
            .catch(err=>{
              console.log('' ,err)
            })
      }
      else if(type==999) {
        let params = [];
        let fitSession;
        if(sessionStorage.getItem('fittingInfo')){
          fitSession = JSON.parse(sessionStorage.getItem('fittingInfo'));
        }
        let dgmList = fitSession;
        if(dgmList && dgmList.length>0){
          dgmList.forEach(dgm=>{
            params.push(dgm.dgm_id.toString())
          });
          params = distinct(params,[]);//
          this.$getApi.queryBoltFittingList(params)
              .then(res=>{
                loadTime = true;
                this.$parent.loading = false;//
                let result = getDgmInfo(res,type);
                this.data = result;
                //
                this.$emit("child-msg", this.type,true)
                // this.filter_by_dgmName();
                this.renderTable()
              })
              .catch(err=>{
                console.log('' ,err)
              })
        }
      }
      else{
        this.$getApi.queryModelByType(type,this.$store.state.tids)
            .then(res=>{
              loadTime = true;
              this.$parent.loading = false;//
              let result = getDgmInfo(res,type)
              if(type==18){
                this.defaultDgmInfo = res.dgm;
              }
              //dgm
              if(type==10 || type==20 || type==21){
                this.tmsList={};//
                this.tmsHostList={};//
                this.tmsPumpList={};//
                this.tmsPreIdList=res.allPre;////id
                let tmsInfo = res.tmsDgmInfo;
                let hostLists = {};//
                let pumpLists = {};//
                if(tmsInfo && tmsInfo.length){
                  tmsInfo.forEach(tms=>{
                    this.tmsList[tms.dgm_id] = tms.dgm_name;
                    hostLists[tms.dgm_id] = tms.main_host;
                    pumpLists[tms.dgm_id] = tms.pump;
                  })
                  for(let key in hostLists){
                    if(hostLists[key] && hostLists[key].length){
                      this.tmsHostList[key] = {};
                      let hostList = hostLists[key];
                      hostList.forEach(host=>{
                        this.tmsHostList[key][host.pre_id] = host.byName;
                      })
                    }
                  }
                  for(let key in pumpLists){
                    if(pumpLists[key] && pumpLists[key].length){
                      this.tmsPumpList[key] = {};
                      let pumpList = pumpLists[key];
                      pumpList.forEach(pump=>{
                        this.tmsPumpList[key][pump.ch_class] = pump.pump_name;
                      })
                    }
                  }
                }
                else{
                  this.$emit("child-msg",this.type,false)
                }
              }
              if(!result.length){
                if(type!=18 && type!=10 && type!=20 && type!=21){
                  this.$emit("child-msg",this.type,false)
                }
                else{
                  //
                  this.renderTable()
                }
              }else{
                this.data = result;
                //
                this.$emit("child-msg", this.type,true)
                this.renderTable()
              }
            })
            .catch(err=>{
              console.log('' ,err)
            })
      }

    },
    /*  */
    renderTable(){
      if(this.$route.path.indexOf('dgm')<0){//
        return;
      }
      let HotTable = document.getElementById('HotTable');
      let property = {
        type:this.type,
        getData:this.getData,
        getHot:this.getHot,
        changeList:this.changeList,
        MHDList:this.MHDList,
        E821List:this.E821List,
        tmsList:this.tmsList,
        tmsHostList: this.tmsHostList,
        tmsPumpList: this.tmsPumpList,
        dgmList:this.dgmList,
        defaultDgmInfo:this.defaultDgmInfo,
        flangeList:this.flangeList,
        fittingInfo:this.$parent.fittingInfo,
        select821Info:this.$parent.select821Info,
        selectDgmInfo:this.$parent.selectDgmInfo,
        btnData:this.$parent.btnData,
        router:this.$router,
      }
      //table
      this.hot = new dgmhdtable(HotTable,property);
      reRenderSelectOption(this.hot,this.data);

      /*  */
      let wtHolder = document.getElementsByClassName('wtHolder')[0]
      wtHolder.addEventListener('scroll',(e)=>{
        let scrollTop = wtHolder.scrollTop;
        let scrollLeft = wtHolder.scrollLeft;
        let paload ={
          scrollType:'dgm',
          type:this.type,
          scrollTop:scrollTop,
          scrollLeft:scrollLeft
        }
        this.$store.commit('setScroll',paload);
        this.hot.destroyEditor()//
      });

      this.filterRows(this.hot);//
    },
    /* dgm_name  */
    filter_by_dgmName(){
      let data = []
      if(!sessionStorage.getItem('dgm_id')) return; /*   */
      let dgm_ids =  JSON.parse(sessionStorage.getItem('dgm_id'))
      if(dgm_ids.length == 0) return /*  */
      dgm_ids.forEach(dgm_id=>{
        this.data.forEach(dgm=>{
          if(dgm.dgm.dgm_id ==dgm_id){
            data.push(dgm)
          }
        })
      })
      this.data = data
    },
    //
    filterRows(hot){
      const filtersPlugin = hot.getPlugin('filters');
      if(sessionStorage.getItem('filter_dgm_name')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_dgm_name'));
        filterArr.forEach(condition =>{
          if(this.type == 1){
            filtersPlugin.addCondition(1,condition.conditions[0].name,condition.conditions[0].args,condition.operations);
          }
          else{
            filtersPlugin.addCondition(0,condition.conditions[0].name,condition.conditions[0].args,condition.operations);
          }
        });
        filtersPlugin.filter();
      }
    },

    //data
    getData(){
      return this.data
    },
    /* hot */
    getHot(){
      return this.hot
    },
  },
}
